const CDN_PATH = "https://cdn.jsdelivr.net/gh/joethun/EmulatorJS-With-Cores/";

declare global {
  interface Window {
    gameRunning: boolean;
    EJS_color: string;
    EJS_backgroundColor: string;
    EJS_player: string;
    EJS_gameName: string;
    EJS_gameUrl: File | string;
    EJS_core: string;
    EJS_pathtodata: string;
    EJS_startOnLoaded: boolean;
    EJS_biosUrl: string;
    EJS_threads: boolean;
    EJS_defaultOptions: Record<string, string>;
    EJS_Buttons: Record<string, any>;
    EJS_emulator?: any;
    showSystemPicker?: (systems: Record<string, string>, callback: (core: string) => void) => void;
    cleanupOldElements?: () => void;
    hideMenu?: () => void;
    createGameDisplay?: () => void;
  }
}

if (typeof window !== 'undefined') {
  window.gameRunning = false;
}

