async function detectCore(extension: string): Promise<string> {
  if (FILE_EXTENSIONS[extension]) {
    return FILE_EXTENSIONS[extension];
  }

  return new Promise((resolve) => {
    if (window.showSystemPicker) {
      window.showSystemPicker(SYSTEM_PICKER, resolve);
    } else {
      // Default fallback
      resolve("nes");
    }
  });
}

function showMenu() {
  // Show all menu elements
  const sidebar = document.querySelector('aside');
  if (sidebar) {
    (sidebar as HTMLElement).style.display = "";
  }
  
  // Show footer
  const footer = document.querySelector('footer');
  if (footer) {
    (footer as HTMLElement).style.display = "";
  }
  
  // Show main content
  const main = document.querySelector('main');
  if (main) {
    (main as HTMLElement).style.display = "";
  }
  
  // Show header
  const header = document.querySelector('header');
  if (header) {
    (header as HTMLElement).style.display = "";
  }
}

function cleanupOldElements() {
  const gameContainer = document.getElementById("game");
  if (gameContainer) {
    gameContainer.innerHTML = "";
    gameContainer.style.display = "none";
  }
  
  // Remove old emulator scripts
  const oldScripts = document.querySelectorAll('script[src*="loader.js"]');
  oldScripts.forEach(script => script.remove());
  
  // Restore menu visibility
  showMenu();
  
  window.gameRunning = false;
}

function hideMenu() {
  if (typeof document === 'undefined') return;
  
  // Hide all menu elements with important styles
  const menu = document.getElementById("menu");
  if (menu) {
    (menu as HTMLElement).style.cssText = "display: none !important; visibility: hidden !important;";
  }
  
  // Hide sidebar
  const sidebar = document.querySelector('aside');
  if (sidebar) {
    (sidebar as HTMLElement).style.cssText = "display: none !important; visibility: hidden !important; z-index: -1 !important;";
  }
  
  // Hide footer
  const footer = document.querySelector('footer');
  if (footer) {
    (footer as HTMLElement).style.cssText = "display: none !important; visibility: hidden !important; z-index: -1 !important;";
  }
  
  // Hide main content
  const main = document.querySelector('main');
  if (main) {
    (main as HTMLElement).style.cssText = "display: none !important; visibility: hidden !important; z-index: -1 !important;";
  }
  
  // Hide header
  const header = document.querySelector('header');
  if (header) {
    (header as HTMLElement).style.cssText = "display: none !important; visibility: hidden !important; z-index: -1 !important;";
  }
  
  // Hide the root div that contains everything
  const rootDiv = document.querySelector('div[style*="min-h-screen"]');
  if (rootDiv) {
    (rootDiv as HTMLElement).style.cssText += " z-index: -1 !important;";
  }
}

function createGameDisplay() {
  if (typeof document === 'undefined') return;
  
  let gameContainer = document.getElementById("game");
  if (!gameContainer) {
    gameContainer = document.createElement("div");
    gameContainer.id = "game";
    (gameContainer as HTMLElement).style.cssText = `
      width: 100%;
      height: 100vh;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 999999;
      background-color: #000000;
      overflow: hidden;
      display: block !important;
      visibility: visible !important;
      opacity: 1 !important;
    `;
    document.body.appendChild(gameContainer);
  } else {
    // Clear existing content and make sure it's visible
    gameContainer.innerHTML = "";
    (gameContainer as HTMLElement).style.cssText = `
      width: 100%;
      height: 100vh;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 999999;
      background-color: #000000;
      overflow: hidden;
      display: block !important;
      visibility: visible !important;
      opacity: 1 !important;
    `;
  }
  
  // Ensure body and html don't have overflow issues
  if (document.body) {
    document.body.style.overflow = "hidden";
  }
  if (document.documentElement) {
    document.documentElement.style.overflow = "hidden";
  }
}

export async function loadGame(file: File | string, coreOverride?: string): Promise<void> {
  let fileName: string;
  let fileExtension: string;
  let gameName: string;
  let gameFile: File | string;

  if (file instanceof File) {
    fileName = file.name;
    fileExtension = fileName.split(".").pop()?.toLowerCase() || "";
    gameName = fileName.substring(0, fileName.lastIndexOf("."));
    gameFile = file;
  } else {
    fileName = file;
    fileExtension = fileName.split(".").pop()?.toLowerCase() || "";
    gameName = fileName.substring(0, fileName.lastIndexOf("."));
    gameFile = file;
  }

  const core = coreOverride || await detectCore(fileExtension);
  
  cleanupOldElements();
  hideMenu();
  createGameDisplay();
  if (typeof window !== 'undefined') {
    window.gameRunning = true;
  }
  
  configureEmulator(gameName, gameFile, core);
  loadEmulatorScript();
}

function configureEmulator(gameName: string, gameFile: File | string, core: string) {
  if (typeof window === 'undefined') return;
  
  window.EJS_color = "#003d8c";
  window.EJS_backgroundColor = "#000000";
  window.EJS_player = "#game";
  window.EJS_gameName = gameName;
  window.EJS_gameUrl = gameFile;
  window.EJS_core = core;
  window.EJS_pathtodata = CDN_PATH;
  window.EJS_startOnLoaded = true;
  window.EJS_biosUrl = "";
  
  const threadsEnabled = ["psp", "dosbox_pure"];
  if (threadsEnabled.includes(core)) {
    window.EJS_threads = true;
  }

  window.EJS_defaultOptions = {
    "save-save-interval": "60",
    "desmume_advanced_timing": "disabled",
    "webgl2Enabled": "enabled",
    ...(core === "segaGG" && { retroarch_core: "genesis_plus_gx" }),
    ...(core === "nds" && { retroarch_core: "desmume" })
  };

  window.EJS_Buttons = {
    exitEmulation: false,
    cacheManager: false,
    saveSavFiles: { visible: false },
    loadSavFiles: { visible: false }
  };
}

function loadEmulatorScript() {
  if (typeof document === 'undefined') return;
  
  const script = document.createElement("script");
  script.src = `${CDN_PATH}/loader.js`;
  script.onload = () => {
    // Ensure game container is visible after script loads
    setTimeout(() => {
      const gameContainer = document.getElementById("game");
      if (gameContainer) {
        (gameContainer as HTMLElement).style.cssText = `
          width: 100% !important;
          height: 100vh !important;
          position: fixed !important;
          top: 0 !important;
          left: 0 !important;
          z-index: 999999 !important;
          background-color: #000000 !important;
          overflow: hidden !important;
          display: block !important;
          visibility: visible !important;
          opacity: 1 !important;
        `;
        
        // Ensure any canvas inside is visible
        const canvas = gameContainer.querySelector('canvas');
        if (canvas) {
          (canvas as HTMLElement).style.cssText = `
            display: block !important;
            visibility: visible !important;
            opacity: 1 !important;
            width: 100% !important;
            height: 100% !important;
          `;
        }
      }
    }, 100);
  };
  document.body.appendChild(script);
}

export async function saveState(): Promise<void> {
  const emulator = window.EJS_emulator;
  if (!emulator?.started) {
    return;
  }

  try {
    const state = emulator.gameManager.getState();
    const stateFileName = `${emulator.getBaseFileName()}.state`;
    await emulator.storage.states.put(stateFileName, state);
    emulator.displayMessage("SAVED STATE TO BROWSER");
  } catch (error: any) {
    console.error("Save failed:", error);
    emulator.displayMessage(`Error saving: ${error.message}`);
  }
}

export async function loadState(): Promise<void> {
  const emulator = window.EJS_emulator;
  if (!emulator?.started) {
    return;
  }

  try {
    const stateFileName = `${emulator.getBaseFileName()}.state`;
    const state = await emulator.storage.states.get(stateFileName);
    if (state) {
      await emulator.gameManager.loadState(state);
      emulator.displayMessage("LOADED STATE FROM BROWSER");
    } else {
      emulator.displayMessage("");
    }
  } catch (error: any) {
    console.error("Load failed:", error);
    emulator.displayMessage(`Error loading: ${error.message}`);
  }
}

export function exitGame() {
  cleanupOldElements();
  const gameContainer = document.getElementById("game");
  if (gameContainer) {
    gameContainer.style.display = "none";
  }
}

export function addEventListeners() {
  document.addEventListener("keydown", (event) => {
    if (event.key === "F1") {
      event.preventDefault();
      saveState();
    } else if (event.key === "F2") {
      event.preventDefault();
      loadState();
    } else if (event.key === "Escape" && window.gameRunning) {
      event.preventDefault();
      exitGame();
    }
  });

  const saveButton = document.getElementById("saveBrowser");
  const loadButton = document.getElementById("loadBrowser");
  
  if (saveButton) {
    (saveButton as HTMLElement).onclick = saveState;
  }
  
  if (loadButton) {
    (loadButton as HTMLElement).onclick = loadState;
  }
}

// Expose functions to window for global access
if (typeof window !== 'undefined') {
  (window as any).saveToBrowserStorage = saveState;
  (window as any).loadFromBrowserStorage = loadState;
  (window as any).loadGame = loadGame;
  (window as any).exitGame = exitGame;
  window.cleanupOldElements = cleanupOldElements;
  window.hideMenu = hideMenu;
  window.createGameDisplay = createGameDisplay;
}

// Initialize event listeners when DOM is ready
if (typeof document !== 'undefined') {
  if (document.readyState === 'loading') {
    document.addEventListener("DOMContentLoaded", addEventListeners);
  } else {
    addEventListeners();
  }
}

