"use client";

import { useState, useEffect, useMemo } from 'react';
import { loadGame } from '@/lib/emulator';

const SYSTEM_PICKER: Record<string, string> = {
  "Nintendo NES": "nes",
  "Nintendo GB": "gb",
  "Nintendo SNES": "snes",
  "Nintendo VB": "vb",
  "Nintendo 64": "n64",
  "Nintendo GBC": "gbc",
  "Nintendo GBA": "gba",
  "Nintendo DS": "nds",
  "Sega MS": "segaMS",
  "Sega Gen/MD": "segaMD",
  "Sega GG": "segaGG",
  "Sega CD": "segaCD",
  "Sega 32X": "sega32x",
  "Sega Saturn": "segaSaturn",
  "Sony PS1": "psx",
  "Sony PSP": "psp",
  "Atari 2600": "atari2600",
  "Atari 5200": "atari5200",
  "Atari 7800": "atari7800",
  "Atari Lynx": "lynx",
  "Atari Jaguar": "jaguar",
  "Panasonic 3DO": "opera",
  "Arcade (FBNeo)": "arcade",
  "Arcade (M.A.M.E)": "mame2003_plus",
  "Microsoft DOS": "dosbox_pure",
  "Commodore PET": "vice_xpet",
  "Commodore VIC20": "vice_xvic",
  "Commodore Amiga": "amiga",
  "Commodore 64": "vice_x64",
  "Commodore 128": "vice_x128",
  "Commodore P/4": "vice_xplus4",
  "ColecoVision": "coleco",
  "NEC TurboGrafx-16": "pce",
  "NEC PC-FX": "pcfx",
  "SNK NGP": "ngp",
  "Bandai WS": "ws"
};

interface IconProps {
  className?: string;
}

const BookOpenIcon = ({ className }: IconProps) => (
  <svg className={className} fill="none" stroke="currentColor" viewBox="0 0 24 24">
    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 6.253v13m0-13C10.832 5.477 9.246 5 7.5 5S4.168 5.477 3 6.253v13C4.168 18.477 5.754 18 7.5 18s3.332.477 4.5 1.253m0-13C13.168 5.477 14.754 5 16.5 5s3.332.477 4.5 1.253v13C19.832 18.477 18.246 18 16.5 18s-3.332.477-4.5 1.253"></path>
  </svg>
);

const ShoppingBagIcon = ({ className }: IconProps) => (
  <svg className={className} fill="none" stroke="currentColor" viewBox="0 0 24 24">
    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z"></path>
  </svg>
);

const DownloadIcon = ({ className }: IconProps) => (
  <svg className={className} fill="none" stroke="currentColor" viewBox="0 0 24 24">
    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-4l-4 4m0 0l-4-4m4 4V4"></path>
  </svg>
);

const MenuIcon = ({ className }: IconProps) => (
  <svg className={className} fill="none" stroke="currentColor" viewBox="0 0 24 24">
    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 6h16M4 12h16M4 18h16"></path>
  </svg>
);

const XIcon = ({ className }: IconProps) => (
  <svg className={className} fill="none" stroke="currentColor" viewBox="0 0 24 24">
    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12"></path>
  </svg>
);

const CogIcon = ({ className }: IconProps) => (
  <svg className={className} fill="none" stroke="currentColor" viewBox="0 0 24 24">
    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M10.325 4.317c.426-1.756 2.924-1.756 3.35 0a1.724 1.724 0 002.573 1.066c1.543-.94 3.31.826 2.37 2.37a1.724 1.724 0 001.065 2.572c1.756.426 1.756 2.924 0 3.35a1.724 1.724 0 00-1.066 2.573c.94 1.543-.826 3.31-2.37 2.37a1.724 1.724 0 00-2.572 1.065c-.426 1.756-2.924 1.756-3.35 0a1.724 1.724 0 00-2.573-1.066c-1.543.94-3.31-.826-2.37-2.37a1.724 1.724 0 00-1.065-2.572c-1.756-.426-1.756-2.924 0-3.35a1.724 1.724 0 001.066-2.573c-.94-1.543.826-3.31 2.37-2.37.996.608 2.296.07 2.572-1.065z"></path>
    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"></path>
  </svg>
);

interface Game {
  id: number;
  title: string;
  genre: string;
  hours: number;
  color: string;
  emoji: string;
  filePath?: string;
  fileName?: string;
  fileData?: string; // Base64 encoded file data
  core?: string; // Emulator core/system
}

const COLORS = {
  highlight: '#cccccc',
  darkBg: '#0f0f0f',
  midDark: '#1e1e1e',
  softLight: '#f5f5f5',
  sidebarHover: '#333333',
  playGreen: '#00ff7f',
};

function GameCard({ game, onPlay, onEdit }: { game: Game; onPlay: (game: Game) => void; onEdit: (game: Game) => void }) {
  const coverClass = useMemo(() => `${game.color} h-full flex items-center justify-center text-6xl transition-all duration-300 group-hover:scale-[1.05]`, [game.color]);
  const cardClass = "group relative overflow-hidden w-64 h-80 rounded-none transition-all duration-300 ease-in-out cursor-pointer hover:scale-[1.03] hover:ring-4 hover:ring-gray-200 hover:ring-offset-0 focus:ring-4 focus:ring-gray-200 border border-transparent";
  const overlayClass = "absolute inset-0 bg-black/30 backdrop-brightness-75 flex flex-col justify-end p-4 opacity-0 group-hover:opacity-100 transition-opacity duration-300";
  const buttonClass = "mt-3 w-full font-bold py-2 px-4 rounded-none transition-colors text-sm transform hover:scale-[1.02]";

  return (
    <div className={cardClass} style={{ backgroundColor: COLORS.midDark }}>
      <div className={coverClass}>{game.emoji}</div>
      <div className={overlayClass}>
        <h3 className="text-xl font-bold truncate mb-1" style={{ color: COLORS.softLight }}>{game.title}</h3>
        <p className="text-sm" style={{ color: COLORS.highlight }}>
          {game.genre} | {game.hours} hrs played
        </p>
        <div className="flex gap-2 mt-3">
          <button
            className={buttonClass + " flex-1"}
            style={{ backgroundColor: COLORS.playGreen, color: COLORS.darkBg }}
            onClick={(e) => { e.stopPropagation(); onPlay(game); }}
          >
            PLAY
          </button>
          <button
            className="px-3 py-2 rounded-none transition-colors"
            style={{ backgroundColor: COLORS.highlight, color: COLORS.darkBg }}
            onClick={(e) => { e.stopPropagation(); onEdit(game); }}
            title="Edit System"
          >
            <CogIcon className="w-5 h-5" />
          </button>
        </div>
      </div>
    </div>
  );
}

const GAME_EXTENSIONS = ['.nes', '.snes', '.gb', '.gbc', '.gba', '.n64', '.z64', '.v64', '.nds', '.3ds', '.iso', '.rom', '.bin', '.smc', '.sfc'];
const COLORS_ARRAY = ['bg-blue-600', 'bg-green-600', 'bg-indigo-600', 'bg-yellow-600', 'bg-red-600', 'bg-purple-600', 'bg-pink-600', 'bg-cyan-600'];
const EMOJIS = ['üéÆ', 'üïπÔ∏è', 'üëæ', 'üéØ', 'üé≤', 'üé™', 'üé®', 'üé≠'];

export default function Home() {
  const [activeView, setActiveView] = useState<'library' | 'store' | 'downloads'>('library');
  const [modalGame, setModalGame] = useState<string | null>(null);
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const [currentTime, setCurrentTime] = useState<string>('');
  const [isMounted, setIsMounted] = useState(false);
  const [games, setGames] = useState<Game[]>([]);
  const [editingGame, setEditingGame] = useState<Game | null>(null);
  const [systemPickerOpen, setSystemPickerOpen] = useState(false);
  const [pendingGame, setPendingGame] = useState<Partial<Game> | null>(null);

  useEffect(() => {
    if (typeof window === 'undefined') return;
    const updateTime = () => {
      setCurrentTime(new Date().toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit' }));
    };
    updateTime();
    setIsMounted(true);
    const interval = setInterval(updateTime, 60000);
    
    // Load games from localStorage on mount
    const savedGames = localStorage.getItem('games');
    if (savedGames) {
      try {
        setGames(JSON.parse(savedGames));
      } catch (e) {
        console.error('Failed to load games from storage', e);
      }
    }
    
    return () => clearInterval(interval);
  }, []);

  const fileToBase64 = (file: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = () => resolve(reader.result as string);
      reader.onerror = reject;
      reader.readAsDataURL(file);
    });
  };

  const getSystemFromExtension = (extension: string): string | null => {
    const FILE_EXTENSIONS: Record<string, string> = {
      fds: "nes", nes: "nes", unif: "nes", unf: "nes",
      gb: "gb", gbc: "gbc", gba: "gba",
      smc: "snes", sfc: "snes", swc: "snes",
      vb: "vb", vboy: "vb",
      z64: "n64", n64: "n64",
      nds: "nds",
      sms: "segaMS", md: "segaMD", gen: "segaMD", smd: "segaMD",
      gg: "segaGG", "32x": "sega32x",
      cso: "psp", pbp: "psp",
      a26: "atari2600", a52: "atari5200", a78: "atari7800",
      lnx: "lynx", j64: "jaguar", jag: "jaguar",
      d64: "vice_x64", t64: "vice_x64", tap: "vice_x64", crt: "vice_x64",
      col: "coleco", cv: "coleco",
      pce: "pce", ngp: "ngp", ngc: "ngp",
      ws: "ws", wsc: "ws"
    };
    return FILE_EXTENSIONS[extension.toLowerCase()] || null;
  };

  const selectSystem = (core: string) => {
    if (editingGame && pendingGame) {
      // Update existing game
      const updatedGames = games.map(g => g.id === editingGame.id ? { ...g, core: core } : g);
      setGames(updatedGames);
      localStorage.setItem('games', JSON.stringify(updatedGames));
      setEditingGame(null);
      setPendingGame(null);
      setSystemPickerOpen(false);
    } else if (pendingGame) {
      // Add new game
      const newGame: Game = {
        ...pendingGame,
        core: core
      } as Game;
      const updatedGames = [...games, newGame];
      setGames(updatedGames);
      localStorage.setItem('games', JSON.stringify(updatedGames));
      setPendingGame(null);
      setSystemPickerOpen(false);
    }
  };

  const handleFileSelect = async () => {
    try {
      if ('showOpenFilePicker' in window) {
        const fileHandles = await (window as any).showOpenFilePicker({
          multiple: true,
          types: [{
            description: 'Game Files',
            accept: {
              'application/octet-stream': GAME_EXTENSIONS
            }
          }]
        });

        for (let i = 0; i < fileHandles.length; i++) {
          const fileHandle = fileHandles[i];
          const file = await fileHandle.getFile();
          const fileName = file.name;
          const nameWithoutExt = fileName.replace(/\.[^/.]+$/, '');
          const fileData = await fileToBase64(file);
          const fileExtension = fileName.split(".").pop()?.toLowerCase() || "";
          const detectedCore = getSystemFromExtension(fileExtension);
          
          const gameData: Partial<Game> = {
            id: Date.now() + i,
            title: nameWithoutExt,
            genre: 'ROM',
            hours: 0,
            color: COLORS_ARRAY[i % COLORS_ARRAY.length],
            emoji: EMOJIS[i % EMOJIS.length],
            filePath: fileHandle.name,
            fileName: fileName,
            fileData: fileData,
            core: detectedCore || undefined
          };
          
          if (detectedCore) {
            // Auto-detected, add directly
            const newGame = gameData as Game;
            const updatedGames = [...games, newGame];
            setGames(updatedGames);
            localStorage.setItem('games', JSON.stringify(updatedGames));
          } else {
            // Need to ask for system
            setPendingGame(gameData);
            setSystemPickerOpen(true);
            // Wait for system selection before continuing
            await new Promise<void>((resolve) => {
              const checkInterval = setInterval(() => {
                if (!systemPickerOpen && !pendingGame) {
                  clearInterval(checkInterval);
                  resolve();
                }
              }, 100);
            });
          }
        }
      } else {
        // Fallback for browsers without File System Access API
        const input = document.createElement('input');
        input.type = 'file';
        input.multiple = true;
        input.accept = GAME_EXTENSIONS.join(',');
        input.onchange = async (e: any) => {
          const files = Array.from(e.target.files || []) as File[];
          for (let i = 0; i < files.length; i++) {
            const file = files[i];
            const nameWithoutExt = file.name.replace(/\.[^/.]+$/, '');
            const fileData = await fileToBase64(file);
            const fileExtension = file.name.split(".").pop()?.toLowerCase() || "";
            const detectedCore = getSystemFromExtension(fileExtension);
            
            const gameData: Partial<Game> = {
              id: Date.now() + i,
              title: nameWithoutExt,
              genre: 'ROM',
              hours: 0,
              color: COLORS_ARRAY[i % COLORS_ARRAY.length],
              emoji: EMOJIS[i % EMOJIS.length],
              filePath: file.name,
              fileName: file.name,
              fileData: fileData,
              core: detectedCore || undefined
            };
            
            if (detectedCore) {
              const newGame = gameData as Game;
              const updatedGames = [...games, newGame];
              setGames(updatedGames);
              localStorage.setItem('games', JSON.stringify(updatedGames));
            } else {
              setPendingGame(gameData);
              setSystemPickerOpen(true);
            }
          }
        };
        input.click();
      }
    } catch (err: any) {
      if (err.name !== 'AbortError') {
        console.error('Error selecting files:', err);
      }
    }
  };

  const handleEditGame = (game: Game) => {
    setEditingGame(game);
    setPendingGame({ ...game });
    setSystemPickerOpen(true);
  };

  const handlePlayClick = async (game: Game) => {
    try {
      if (game.fileData) {
        // Convert base64 data URL back to File
        const response = await fetch(game.fileData);
        const blob = await response.blob();
        const file = new File([blob], game.fileName || game.title, { type: 'application/octet-stream' });
        await loadGame(file, game.core);
        setModalGame(null); // Close modal and start game
      } else {
        setModalGame(game.title);
      }
    } catch (error) {
      console.error('Error loading game:', error);
      setModalGame(game.title);
    }
  };

  const closeModal = () => {
    setModalGame(null);
  };

  const handleNavClick = (viewName: 'library' | 'store' | 'downloads') => {
    setActiveView(viewName);
    setIsSidebarOpen(false);
  };

  const sidebarBaseClass = "sidebar-item block p-3 mb-2 rounded-none transition-all flex items-center border-l-4";
  const sidebarActiveClass = "active border-l-4";
  const sidebarInactiveClass = "border-transparent";

  const libraryButtonClass = useMemo(() => {
    return activeView === 'library' 
      ? `${sidebarBaseClass} ${sidebarActiveClass}` 
      : `${sidebarBaseClass} ${sidebarInactiveClass}`;
  }, [activeView]);

  const storeButtonClass = useMemo(() => {
    return activeView === 'store' 
      ? `${sidebarBaseClass} ${sidebarActiveClass}` 
      : `${sidebarBaseClass} ${sidebarInactiveClass}`;
  }, [activeView]);

  const downloadsButtonClass = useMemo(() => {
    return activeView === 'downloads' 
      ? `${sidebarBaseClass} ${sidebarActiveClass}` 
      : `${sidebarBaseClass} ${sidebarInactiveClass}`;
  }, [activeView]);

  const sidebarClass = useMemo(() => {
    return isSidebarOpen ? "translate-x-0" : "-translate-x-full";
  }, [isSidebarOpen]);

  const sidebarFullClass = useMemo(() => {
    return `w-64 p-6 flex flex-col justify-start shadow-xl fixed left-0 top-0 bottom-0 z-50 transition-transform duration-300 ease-in-out ${sidebarClass}`;
  }, [sidebarClass]);

  const renderContent = () => {
    if (activeView === 'store') {
      return (
        <div>
          <p className="text-lg mt-10 p-6 rounded-none" style={{ backgroundColor: COLORS.midDark, color: COLORS.highlight }}>
            Welcome to the Store! Discover new games and DLC here.
          </p>
        </div>
      );
    }
    if (activeView === 'downloads') {
      return (
        <div>
          <p className="text-lg mt-10 p-6 rounded-none" style={{ backgroundColor: COLORS.midDark, color: COLORS.highlight }}>
            No active downloads. You are ready to play!
          </p>
        </div>
      );
    }
    return (
      <div>
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-xl font-semibold" style={{ color: COLORS.highlight }}>
            Installed Games ({games.length})
          </h2>
          <button
            onClick={handleFileSelect}
            className="px-4 py-2 rounded-none font-semibold transition-colors"
            style={{ backgroundColor: COLORS.playGreen, color: COLORS.darkBg }}
          >
            Add Games
          </button>
        </div>
        {games.length === 0 ? (
          <div className="text-center py-12">
            <p className="text-lg mb-4" style={{ color: COLORS.highlight }}>No games found</p>
            <button
              onClick={handleFileSelect}
              className="px-6 py-3 rounded-none font-semibold transition-colors"
              style={{ backgroundColor: COLORS.playGreen, color: COLORS.darkBg }}
            >
              Add Your First Game
            </button>
          </div>
        ) : (
          <div className="flex flex-wrap" style={{ gap: '0.75rem' }}>
            {games.map(game => (
              <GameCard key={game.id} game={game} onPlay={handlePlayClick} onEdit={handleEditGame} />
            ))}
          </div>
        )}
      </div>
    );
  };

  return (
    <div className="min-h-screen flex flex-col" style={{ backgroundColor: COLORS.darkBg, fontFamily: 'Inter, sans-serif' }}>
      {isSidebarOpen && (
        <div className="fixed inset-0 bg-black/50 z-40" onClick={() => setIsSidebarOpen(false)} />
      )}
      <aside className={sidebarFullClass} style={{ backgroundColor: COLORS.midDark }}>
        <div className="text-3xl font-bold mb-10" style={{ color: COLORS.highlight }}>
          joe t emulator
        </div>
        <nav>
          <button
            className={libraryButtonClass}
            style={{
              backgroundColor: activeView === 'library' ? COLORS.sidebarHover : 'transparent',
              borderLeftColor: activeView === 'library' ? COLORS.highlight : 'transparent',
              color: COLORS.softLight
            }}
            onClick={() => handleNavClick('library')}
          >
            <BookOpenIcon className="w-6 h-6 mr-3" />
            <span className="font-semibold">Library</span>
          </button>
          <button
            className={storeButtonClass}
            style={{
              backgroundColor: activeView === 'store' ? COLORS.sidebarHover : 'transparent',
              borderLeftColor: activeView === 'store' ? COLORS.highlight : 'transparent',
              color: COLORS.softLight
            }}
            onClick={() => handleNavClick('store')}
          >
            <ShoppingBagIcon className="w-6 h-6 mr-3" />
            <span className="font-semibold">Store</span>
          </button>
          <button
            className={downloadsButtonClass}
            style={{
              backgroundColor: activeView === 'downloads' ? COLORS.sidebarHover : 'transparent',
              borderLeftColor: activeView === 'downloads' ? COLORS.highlight : 'transparent',
              color: COLORS.softLight
            }}
            onClick={() => handleNavClick('downloads')}
          >
            <DownloadIcon className="w-6 h-6 mr-3" />
            <span className="font-semibold">Downloads</span>
          </button>
        </nav>
      </aside>
      <div className="flex-1 overflow-hidden">
        <main className="p-8 overflow-y-auto pb-20" style={{ minHeight: 'calc(100vh - 4rem)' }}>
          <header className="mb-8">
            <div className="flex justify-between items-center">
              <h1 className="text-4xl font-extrabold" style={{ color: COLORS.softLight }}>
                {activeView.charAt(0).toUpperCase() + activeView.slice(1)}
              </h1>
              <div className="flex items-center space-x-3">
                {isMounted && (
                  <div className="hidden sm:block text-xs font-normal" style={{ color: COLORS.highlight }}>
                    {currentTime}
                  </div>
                )}
                <div className="hidden sm:block">
                  <p className="font-semibold text-sm" style={{ color: COLORS.softLight }}>Player One</p>
                </div>
                <div className="w-10 h-10 rounded-none flex items-center justify-center font-bold text-lg" style={{ backgroundColor: COLORS.highlight, color: COLORS.darkBg }}>P</div>
              </div>
            </div>
          </header>
          {renderContent()}
        </main>
      </div>
      <footer className="fixed bottom-0 left-0 right-0 h-16 border-t flex items-center justify-between px-6 z-50" style={{ backgroundColor: COLORS.midDark, borderColor: COLORS.sidebarHover }}>
        <button
          className="p-2 rounded-none transition-colors hover:bg-gray-700"
          onClick={() => setIsSidebarOpen(!isSidebarOpen)}
          style={{ color: COLORS.highlight, backgroundColor: isSidebarOpen ? COLORS.sidebarHover : 'transparent' }}
        >
          {isSidebarOpen ? <XIcon className="w-8 h-8" /> : <MenuIcon className="w-8 h-8" />}
        </button>
        <div className="flex-1 mx-4" />
      </footer>
      {modalGame && (
        <div className="fixed inset-0 bg-black/50 backdrop-brightness-50 flex items-center justify-center z-50">
          <div className="p-8 rounded-none max-w-sm w-full shadow-2xl border-2" style={{ backgroundColor: COLORS.midDark, borderColor: COLORS.highlight }}>
            <h3 className="text-2xl font-bold mb-4" style={{ color: COLORS.softLight }}>Launch Game</h3>
            <p className="mb-6" style={{ color: COLORS.highlight }}>You are trying to launch <strong>{modalGame}</strong>.</p>
            <p className="text-sm italic mb-8" style={{ color: COLORS.highlight }}>
              (In a real app, this would start the game process!)
            </p>
            <div className="flex justify-end space-x-3">
              <button
                className="py-2 px-4 rounded-none font-semibold transition-colors bg-gray-700 hover:bg-gray-600"
                style={{ color: COLORS.softLight }}
                onClick={closeModal}
              >
                Cancel
              </button>
              <button
                className="py-2 px-4 rounded-none font-semibold transition-colors"
                style={{ backgroundColor: COLORS.playGreen, color: COLORS.darkBg }}
                onClick={closeModal}
              >
                Launch Now
              </button>
            </div>
          </div>
        </div>
      )}
      {systemPickerOpen && (
        <div className="fixed inset-0 bg-black/50 backdrop-brightness-50 flex items-center justify-center z-50">
          <div className="p-8 rounded-none max-w-2xl w-full max-h-[80vh] overflow-y-auto shadow-2xl border-2" style={{ backgroundColor: COLORS.midDark, borderColor: COLORS.highlight }}>
            <h3 className="text-2xl font-bold mb-4" style={{ color: COLORS.softLight }}>
              {editingGame ? 'Select System for ' + editingGame.title : 'Select System'}
            </h3>
            <div className="grid grid-cols-2 sm:grid-cols-3 gap-3 mb-6">
              {Object.entries(SYSTEM_PICKER).map(([name, core]) => {
                const currentCore = editingGame?.core || pendingGame?.core;
                const isSelected = currentCore === core;
                return (
                  <button
                    key={core}
                    className="p-4 rounded-none text-left transition-colors border-2"
                    style={{
                      backgroundColor: isSelected ? COLORS.playGreen : COLORS.sidebarHover,
                      borderColor: isSelected ? COLORS.playGreen : COLORS.highlight,
                      color: isSelected ? COLORS.darkBg : COLORS.softLight
                    }}
                    onClick={() => selectSystem(core)}
                  >
                    <div className="font-semibold text-sm">{name}</div>
                  </button>
                );
              })}
            </div>
            <div className="flex justify-end">
              <button
                className="py-2 px-4 rounded-none font-semibold transition-colors"
                style={{ backgroundColor: COLORS.highlight, color: COLORS.darkBg }}
                onClick={() => {
                  setSystemPickerOpen(false);
                  setPendingGame(null);
                  setEditingGame(null);
                }}
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
